import uniPopup from "../../components/uni-ui/uni-popup/uni-popup.vue"
import uniIcons from '../../components/uni-ui/uni-icons/uni-icons.vue'
import uniGoodsNav from '../../components/uni-ui/uni-goods-nav/uni-goods-nav.vue'
import titleLine from "../../components/common/titleLine.vue"
import parser from "@/components/jyf-parser/jyf-parser";
import goodBox from "../../components/common/goodBox.vue"
import uniNumberBox from "../../components/uni-ui/uni-number-box/uni-number-box.vue"
export default {
	components: {
		parser,
		titleLine,
		goodBox,
		uniGoodsNav,
		uniIcons,
		uniPopup,
		uniNumberBox
	},
	data() {
		return {
			id: null,
			detailData: null,
			content: null,
			winHeight: 0,
			showFlag: false,
			goodNum: 1,
			openId: '',
			user: '',
			tagStyle: {
				img: 'display:block'
			},
			options: [{
				icon: 'heart', //heart-filled
				text: '收藏'
			}, {
				icon: 'cart',
				text: '购物车',
				info: 0
			}],
			buttonGroup: [{
					text: '加入购物车',
					backgroundColor: '#ffa200',
					color: '#fff'
				},
				{
					text: '立即购买',
					backgroundColor: '#ff0000',
					color: '#fff'
				}
			]
		};
	},
	methods: {
		onClick(e) {
			if (this.user !== '') {
				//区分点击收藏和购物车
				if (e.content.text === '收藏') {
					//如果未收藏则添加收藏，反则提示已收藏
					if (this.options[0].icon === 'heart') {
						this.$api.getIsCollect({
							goodsId: this.id,
							openId: this.openId
						}).then(res => {
							if (res.data.data === "success") {
								uni.showToast({
									title: "收藏成功",
									duration: 800
								})
								this.options[0].icon = "heart-filled"
							}
						})
					} else {
						this.$api.getIsCollect({
							goodsId: this.id,
							openId: this.openId
						}).then(res => {
							if (res.data.data === "success") {
								uni.showToast({
									title: "取消收藏",
									duration: 800
								})
								this.options[0].icon = "heart"
							}
						})
						// uni.showToast({
						// 	icon: "none",
						// 	title: "该商品已经被收藏了",
						// 	duration: 800
						// })
					}
				} else {
					//如果购物车有物品则跳转，没有则提示
					if (this.options[1].info === 0) {
						uni.showToast({
							icon: "none",
							title: "购物车内暂无商品",
							duration: 800
						})
					} else {
						uni.switchTab({
							url: "/pages/shoppingCart/shoppingCart"
						})
					}
				}
			} else {
				uni.showToast({
					icon: "none",
					title: "您还没有登录",
					duration: 800
				})
			}
		},
		buttonClick(e) {
			if (this.user !== '') {
				if (this.showFlag) {
					if (e.index === 0) {
						this.$api.getAddCart({
							goodsId: this.id,
							number: Number(this.goodNum),
							openId: this.openId
						}).then(res => {
							this.getNum()
						})
					} else {
						let paylist = JSON.stringify([{
							goods_id: this.id,
							goods_name: this.detailData.info.name,
							list_pic_url: this.detailData.info.list_pic_url,
							number: this.goodNum,
							retail_price: this.detailData.info.retail_price,
							type: 'payNow'
						}])
						uni.navigateTo({
							url: `/pages/payList/payList?paylist=${paylist}`
						})
					}
				} else {
					this.openPopup()
				}
			} else {
				uni.showToast({
					icon: "none",
					title: "您还没有登录",
					duration: 800
				})
			}

		},
		openPopup() {
			if (this.user !== '') {
				this.$refs.popup.open()
				this.showFlag = true
			} else {
				uni.showToast({
					icon: "none",
					title: "您还没有登录",
					duration: 800
				})
			}

		},
		changeNum(e) {
			this.goodNum = e
			// console.log(this.goodNum, e);
		},
		getDetail(id) {
			this.$api.getProductDetails({
				id: id,
				openId: this.openId
			}).then(res => {
				if (res.data) {
					this.detailData = res.data
					this.content = res.data.info.goods_desc
					//获取收藏状态
					this.$api.getCollectList(this.openId).then(res => {
						res.data.collectGoodsList.some(item => {
							if (item.id === Number(id)) {
								this.options[0].icon = "heart-filled"
							}
						})
					})
					//获取购物车数量
					this.getNum()
				}
			})
		},
		getNum() {
			this.$api.getCartList(this.openId).then(res => {
				this.options[1].info = res.data.data.length
			})
		}
	},
	onLoad(options) {
		this.id = options.id
		this.openId = uni.getStorageSync('openId')
		this.user = uni.getStorageSync('user')
		this.getDetail(options.id)
		this.winHeight = uni.getSystemInfoSync().windowHeight - 50
	},
	onShow() {
		console.log(11);
		if (this.user !== '') {
			console.log("算商品");
			this.$api.getCartList(uni.getStorageSync('openId')).then(res => {
				if (res.data.data) {
					uni.setTabBarBadge({
						index: 3,
						text: String(res.data.data.length)
					})
					this.options[1].info = Number(res.data.data.length)
				}
			})
		}

	}
}
